<template>
  <div>
    <el-date-picker v-model="year" type="year" placeholder="选择年" style="display: block;"></el-date-picker>

    <p class="text-center caption-color">库存周转天数分析</p>
    <el-table :data="tableData1" border style="width: 100%" :header-cell-style="hdStyle" :cell-style="cellStyle">
      <el-table-column v-for="(item ,index) in tableCloumn1" :key="index" :prop="item.prop" :label="item.label"/>
    </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      hdStyle: {
        backgroundColor: '#fafafa',
        textAlign: 'center',
      },
      cellStyle: {
        textAlign: 'center'
      },
      year: new Date(),
      tableCloumn1: [
        { prop: 'col1', label: '月' },
        { prop: 'col2', label: '库存周转天数' },
      ],
      tableData1: [
        { col1: '1月', col2: ''},
      ],

    }
  }
}
</script>

<style>

</style>



